@-webkit-keyframes swim {
0% {-webkit-transform: translate3d(0px,30px,0px);transform: translate3d(0px,30px,0px);
}
20% {-webkit-transform: translate3d(0px,0px,50px);transform: translate3d(0px,0px,50px);
}
60% {-webkit-transform: translate3d(30px,30px,0px);transform: translate3d(30px,30px,0px);
}
100% {-webkit-transform: translate3d(0px,30px,0px);transform: translate3d(0px,30px,0px);
}
}
@keyframes swim {
0% {-webkit-transform: translate3d(0px,30px,0px);transform: translate3d(0px,30px,0px);
}
20% {-webkit-transform: translate3d(0px,0px,50px);transform: translate3d(0px,0px,50px);
}
60% {-webkit-transform: translate3d(30px,30px,0px);transform: translate3d(30px,30px,0px);
}
100% {-webkit-transform: translate3d(0px,30px,0px);transform: translate3d(0px,30px,0px);
}
}
@-webkit-keyframes swimback {
0% {-webkit-transform: translate3d(30px,30px,30px);transform: translate3d(30px,30px,30px);
}
20% {-webkit-transform: translate3d(20px,20px,50px);transform: translate3d(20px,20px,50px);
}
60% {-webkit-transform: translate3d(10px,10px,50px);transform: translate3d(10px,10px,50px);
}
100% {-webkit-transform: translate3d(30px,30px,30px);transform: translate3d(30px,30px,30px);
}
}
@keyframes swimback {
0% {-webkit-transform: translate3d(30px,30px,30px);transform: translate3d(30px,30px,30px);
}
20% {-webkit-transform: translate3d(20px,20px,50px);transform: translate3d(20px,20px,50px);
}
60% {-webkit-transform: translate3d(10px,10px,50px);transform: translate3d(10px,10px,50px);
}
100% {-webkit-transform: translate3d(30px,30px,30px);transform: translate3d(30px,30px,30px);
}
}
@-webkit-keyframes movement {
0% {right:-100px;
}
100% {right:110%;
}
}
@keyframes movement {
0% {right:-100px;
}
100% {right:110%;
}
}
